<template>
  <div class="blog-wrap" v-bind:ref="'blogList'">
    <transition name="fade" mode="out-in">
      <div v-if="!loading">
        <section v-if="notFound">
          <p class="no-posts">Looks like that post is missing</p>
          <router-link :to="'/' + pageType.toLowerCase() + '/'" :title="pageType" class="return"><icon name="chevron-left" class="icon"></icon>&nbsp;&nbsp;Return to {{pageType}}</router-link>
        </section>
        <section v-else>
          <div class="return-wrap">
            <router-link :to="'/' + pageType.toLowerCase() + '/'" :title="pageType" class="return"><icon name="chevron-left" class="icon"></icon>&nbsp;&nbsp;Return to {{pageType}}</router-link>
          </div>
          <blog-post :id="blog.id" :title="blog.title" :age="parseDate(blog.published)" :datetime="blog.published" :path="parsePath(blog.url)" :description="blog.content" :labels="blog.labels" :pageType="pageType"></blog-post>
        </section>
      </div>
      <loading-spinner v-else></loading-spinner>
    </transition>
  </div>
</template>

<script>
import BlogPost from './BlogPost'
import api from '../../gateways/blogger'
import LoadingSpinner from '../global/Loading'
import MomentTZ from 'moment-timezone'
export default {
  name: 'blog',
  components: {BlogPost, LoadingSpinner},
  props: ['pageType'],
  methods: {
    parseDate: function (dateStr) {
      return MomentTZ.tz(dateStr, MomentTZ.tz.guess()).format('MMM D, YYYY')
    },
    parsePath: function (url) {
      return url.split('blogspot.com/')[1].split('.html')[0]
    },
    loadBlog: function () {
      this.loading = true
      var pathStr = '/' + this.$route.params.year + '/' + this.$route.params.month + '/' + this.$route.params.blogId
      api.getBlogPost(pathStr.toLowerCase()).then(function (results) {
        this.blog = results.body
        this.notFound = false
        this.loading = false
      }.bind(this)).catch(function (results) {
        if (results.status === 500 && !this.requestStop) {
          this.requestStop = true
          this.loadBlog()
        } else {
          this.notFound = true
          this.loading = false
        }
      }.bind(this))
    }
  },
  data () {
    return {
      blog: [],
      loading: true,
      notFound: false,
      requestStop: false
    }
  },
  mounted: function () {
    this.loadBlog()
  },
  watch: {
    '$route' (to, from) {
      this.loadBlog()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
.blog-wrap{
  background-color: $dark-purple;
  padding: $home-section-padding;
  flex: 1 0 auto;
}
.no-posts {
  margin-top: 35px;
  color: $font-white;
  font-size: 1.5vw;
}
@media (max-width: 1200px) {
  .no-posts {
    font-size: 2vw;
  }
}
@media (max-width: 1000px) {
  .no-posts {
    font-size: 3vw;
  }
}
@media (max-width: 700px) {
  .no-posts {
    font-size: 4vw;
  }
}
@media (max-width: 500px) {
  .no-posts {
    font-size: 4.5vw;
  }
}
</style>



