<template>
  <article class="blog-post">
  	<router-link :to="'/' + pageType.toLowerCase() + '/blog/' + path" :aria-label="'blog title -' + title + ', blog published - ' + age" :title="title">
      <h3 class="blog-title">{{title}}</h3>
    </router-link>
  	<div>
      <icon name="clock-o" class="icon"></icon><time :datetime="datetime">{{age}}</time></span>
    </div>
    <div v-show="labels && labels.length > 0" class="label-section">
      <span>labels:</span>
      <div class="label-wrap">
        <span v-for="label in labels" v-bind:class="{ pinned: 'pinned' === label.toLowerCase() }" class="label">{{label}}</span>
      </div>
    </div>
  	<div v-html="styled" class="blog-clear"></div>
  </article>
</template>

<script>
export default {
  name: 'blogpost',
  props: ['title', 'age', 'datetime', 'description', 'labels', 'path', 'pageType'],
  methods: {
    evalEmbedds: function () {
      var regex = /<script.*src="(.*?\.js)".*?><\/script>/g
      var matches = null
      do {
        matches = regex.exec(this.styled)
        if (matches !== null && matches.length > 0) {
          var head = document.getElementsByTagName('head')[0]
          var script = document.createElement('script')
          script.type = 'text/javascript'
          script.src = matches[1]

          // find and remove duplicates
          for (var i = 0; i < head.children.length; i++) {
            if (head.children[i].src === matches[1]) {
              head.removeChild(head.children[i])
            }
          }

          // reload script
          head.appendChild(script)
        }
      } while (matches !== null && matches.length > 0)
    }
  },
  computed: {
    styled: function () {
      var reg = /background-color.*?;|font-family.*?;/g
      var imgReg = /;"><img/g
      var temp = this.description.replace(reg, '')
      return temp.replace(imgReg, '; max-width: 100%;"><img')
    }
  },
  mounted () {
    this.evalEmbedds()
  },
  beforeUpdate () {
    this.evalEmbedds()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../../variables.scss';
.embed {
  position: relative;
  width: 100%;
  overflow: hidden;
  min-width: 360px;
  max-width: 720px;
}

.embed:before{
  content: "";
  display: block;
  padding-top: 56.25%; /* 16:9 */
}

.embed iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.blog-post {
	background-color: $font-white;
  max-width: 50em;
  border-radius: 15px;
  transition: $standard-transition;
  margin: $standard-spacing2 auto;
  padding: $standard-spacing3 $standard-spacing4;
  text-align: left;
  .icon {
    fill: $font-gray;
    vertical-align: bottom;
    margin-right: $standard-spacing-half;
  }
  .blog-title {
    display: inline-block;
    color: $dark-purple;
    font-size: 2.25em;
    margin: 0 0 $standard-spacing-half 0;
    &:hover{
      color: $light-purple;
    }
  }
  .blog-clear {
    text-align: initial;
    margin-top: $standard-spacing;
    position: relative;
    word-wrap: break-word;
    word-break: break-word;
    &:after{
      content: '';
      clear: both; 
      display: block;
    }
    a{
      color: $light-purple;
      &:hover,
      &:focus {
        color: $dark-purple;
        text-decoration: underline;
      }
    }
    img{
      height: auto;
      max-width: 89%;
    }
  }
  .label{
    border-radius: 25px;
    margin-right: $standard-spacing-half;
    transition: $standard-transition;
    font-family: inherit;
    letter-spacing: inherit;
    color: $font-gray;
    &.pinned{
      background-color: $light-purple;
      color: $font-white;
      padding: 0 $standard-spacing;
      float: left;
    }
    &-wrap{
      display: inline-block;
      width: calc(100% - 160px);
      vertical-align: top;
    }
    &-section{
      margin-top: $standard-spacing-half;
    }
  }
}
@media (max-width: 520px) {
  .blog-post {
    padding: $standard-spacing3 $standard-spacing2;
  }
}
</style>



