
import Vue from 'vue'
var apiKey = 'AIzaSyBCZKze-EwYoM0NnZRCd2bxGmhgCibH18c'
var blogId = '3895556341811812676'

export default {
  getAllBlogPosts (labels) {
    var url = 'https://www.googleapis.com/blogger/v3/blogs/' + blogId + '/posts?labels=' + labels + '&key=' + apiKey + '&maxResults=30'
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  },
  getBlogPost (path) {
    var url = 'https://www.googleapis.com/blogger/v3/blogs/' + blogId + '/posts/bypath?path=' + path + '.html&key=' + apiKey
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  }
}



